<template>
  <div class="hello">
    <!-- <h1>{{ msg }}</h1> -->
    <h1>Thêm Thông tin Khách hàng</h1>
    <div class="form-group">
      <label for="userName">Your UserName:</label>
      <input type="text"
            id="userName"
            class="form-control"
            v-model="userData.name">
    </div>
    <div class="form-group">
      <label for="phoneNumber">Your phone Number:</label>
      <input type="text"
            id="phoneNumber"
            class="form-control"
            v-model="userData.phoneNumber">
    </div>
    
    <button class="btn btn-primary"
          @click.prevent="submitted">Submit!
    </button>
  
    <div class="card" v-if="isSubmitted">
        <div class="card-body">
            <h4 class="card-title">Result Form Data</h4>
            name: {{userData.name}}
            phone: {{userData.phoneNumber}}
        </div>
    </div>
  </div>
 
</template>

<script>
import axios from 'axios';
export default {
  name: 'HelloWorld',
  data() {
    return {
      userData: {
        name: '',
        phoneNumber:0,
        placeOrder:'',
        orderNumber:0,
        result: {}
      },
      errors: [],
      isSubmitted:false,
      users: []
    }
  }, 
  props: {
    msg: String
  },
  methods: {
    submitted(){
      // let uri = 'http://localhost:1234/khachhang/create'
      let uri = 'http://damp-temple-98347.herokuapp.com:1234/khachhang/create'
      axios.post(uri,{
        name: this.userData.name,
        phoneNumber: this.userData.phoneNumber,
        address: this.userData.address,
        placeOrder: this.userData.placeOrder,
        orderNumber: this.userData.orderNumber
      })
      .then(response => {
        // JSON responses are automatically parsed.
        this.users = response.data;
        this.isSubmitted = true;
      })
      .catch(e => {
        this.errors.push(e)
      })
      
    }
  }
}
</script>

